.timer {
  display: flex;
  text-align: center;
  overflow: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 40px;
  color: white;
  font-weight: 900;
  justify-content: center;
  width: 370px;
}

.time-number {
  transition: all 0.5s;
}

.timer button {
  width: 65px;
  height: 65px;
  font-size: 20px;
  border-radius: 50%;
  border: 0px;
  background-color: palevioletred;
  color: white;
  margin-right: 20px;
}

.timer button::after {
  content: "";
  /* height: 100%;
    width: 100%; */
  height: 120;
  width: 120;
  border-radius: 50%;

  /* z-index: -1; */
  transition: all 0.4s;
  background-color: tomato;
}

.timer button:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}
